<template>
  <div class="tab-navigation">
    <tab-navigation-item v-for="(item, index) in list" :key="item.name">
      <div
        class="item"
        @click="goto(item, index)"
        :class="index === currentIndex ? 'activeClass' : ''"
      >
        <p style="width: 60px"></p>
        <p>{{ item.name }}</p>
      </div>
    </tab-navigation-item>
  </div>
</template>

<script>
import TabNavigationItem from "./TabNavigationItem.vue";

export default {
  name: "e-TabNavigation",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  methods: {
    goto(item, index) {
      this.currentIndex = index;
      window.localStorage.setItem("currentIndex010003", index);
      this.$router.replace(item.path);
    },
  },
  components: {
    TabNavigationItem,
  },
};
</script>

<style scoped>
.tab-navigation {
  margin-top: 60px;
}
.item {
  width: 100%;
  height: 60px;
  font-size: 20px;
  line-height: 60px;
  font-weight: lighter;

  display: flex;
}
.activeClass {
  background-color: var(--color-background-button-green);
}
</style>
