<template>
  <div id="basicFrame">
    <div class="left">
      <div class="left-top">
        <a-tooltip
          :getPopupContainer="
            (triggerNode) => {
              return triggerNode.parentNode;
            }
          "
        >
          <template #title>{{ getAccount }}</template>
          <p class="accountName">{{ getAccount }}</p>
        </a-tooltip>
        <p @click="logout" class="logout">退出</p>
        <p @click="this.$router.replace('/tools/admin/category/update')">
          管理员端
        </p>
      </div>
      <tab-navigation :list="list" ref="tabNavigation"></tab-navigation>
    </div>
    <div class="right">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import TabNavigation from "./childComponents/tabNavigation/TabNavigation.vue";
export default {
  name: "AuditBasicLayout",
  created() {
    this.$nextTick(() => {
      if (!window.localStorage.getItem("currentIndex010003")) {
        // 说明是首次进入这个页面
        window.localStorage.setItem("currentIndex010003", 0);
      } else {
        // 刷新当前页面，本组件重新加载
        const index = parseInt(
          window.localStorage.getItem("currentIndex010003")
        );
        this.$refs.tabNavigation.currentIndex = index;
      }
    });
  },
  data() {
    return {
      list: [
        {
          name: "热门类别",
          path: "/tools/audit/category/update",
        },
        {
          name: "活动特色",
          path: "/tools/audit/feature/update",
        },
        {
          name: "资质荣誉",
          path: "/tools/audit/qualification/update",
        },
        {
          name: "优惠券",
          path: "/tools/audit/coupon/update",
        },
        {
          name: "主理人",
          path: "/tools/audit/provider/update",
        },
        {
          name: "产品详情",
          path: "/tools/audit/product/update",
        },
        {
          name: "提交保费",
          path: "/tools/audit/product/underwriting",
        },
        {
          name: "公告",
          path: "/tools/audit/product/info",
        },
        {
          name: "评价审核",
          path: "/tools/audit/comment",
        },
        {
          name: "基本信息审核",
          path: "/tools/audit/settings",
        },
        {
          name: "私信",
          path: "/tools/audit/product/message",
        },
      ],
    };
  },
  methods: {
    logout() {
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010003");
      /* this.cookieOperations.clearCookie('userId');
      this.cookieOperations.clearCookie('loginName');
      this.cookieOperations.clearCookie('authToken');
      this.cookieOperations.clearCookie('isStaff');
      this.cookieOperations.clearCookie('isSuperuser');
      this.cookieOperations.clearCookie('isProvider'); */
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
      this.$router.replace("/tools/login");
    },
  },
  computed: {
    getAccount() {
      // return this.cookieOperations.getCookie('loginName');
      return window.localStorage.getItem("userName");
    },
  },
  components: {
    TabNavigation,
  },
};
</script>
